var that;
import data from '../data.js';
var top = 0;
import refresh from '@/components/refresh/refresh.vue';
export default {
	name: 'goodsIndex',
	components: { refresh },
	onPageScroll(e) {
		if (e.scrollTop < 1) {
			top = 0;
			uni.$emit('reMsg', 0);
		} else {
			if (top != 1) {
				top = 1;
				uni.$emit('reMsg', 1);
			}
		}
	},
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: this.CustomBarm,
			TabCur: 0,
			scrollLeft: 0,
			banner_list: [],
			banner_list1: [],
			list: [],
			goods_list: [],
			goods_list1: [],
			tablist: [],
			modalName: '',
			classList: [
				{
					name: '无烟艾灸盒'
				},
				{
					name: '艾艾贴'
				},
				{
					name: '黑蚂蚁透骨贴'
				},
				{
					name: '艾灸器具'
				},
				{
					name: '艾草制品'
				},
				{
					name: '艾草用品'
				},
				{
					name: '艾灸盒'
				},
				{
					name: '艾灸设备'
				}
			]
		};
	},
	methods: {
		showModel(e) {
			that.modalName = e.currentTarget.dataset.name;
		},
		hideModal() {
			that.modalName = '';
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
		},
		tpinfo(e) {
			let url = e.currentTarget.dataset.url;
			uni.navigateTo({
				url: url
			});
		},
		tomore(e) {
			uni.navigateTo({
				url: '/pages/goods/goodsList/goodsList'
			});
		},
		getindex() {
			that.$request
				.request({
					url: that.api.default.index,
					data: {
						page_id: -1
					}
				})
				.then(res => {
					console.log('首页：', res);
					that.banner_list = res.banner_list;
					that.banner_list1 = [
						{
							pic_url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg',
							url: '/pages/goods/groupList/groupList'
						},
						{
							pic_url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg',
							url: '/pages/goods/timelimitList/timelimitList'
						}
					];
					// that.tablist = res.nav_icon_list;
					that.goods_list = res.new_goods.slice(0, 3);
					that.goods_list1 = res.hot_goods;
					uni.stopPullDownRefresh();
				});
		}
	},
	onLoad() {
		that = this;
		that.tablist = data.indextablist;
		that.getindex();
	},
	onPullDownRefresh() {
		that.getindex();
		that.$zax.getConfig();
	}
};
